import React from 'react'
import style from './Header.module.css'

function Header({ contact, title, connect, resource }) {
  return (
    <header id={style.header}>
      <div id={style.contact}>
        {contact.map((element, index) => {
          if (element.type === 'Link') {
            return (
              <div key={index}>
                <a href={element.link}>{element.text}</a>
              </div>
            )
          } else {
            return (
              <div key={index}>
                <span key={index}>{element.text}</span>
              </div>
            )
          }
        })}
      </div>
      <div id={style.containerTitre}>
        <div>
          <h1 id={style.titre}>{title.title}</h1>
          <h3>{title.subtitle}</h3>
        </div>
        {connect.connected ? (
          connect.connectedComponentbyDefault ? (
            <div className={style.h100}>
              <div id={style.ConnectedDiv}>
                <span>{connect.pseudo[0]}</span>
              </div>
              <div>
                <a href={connect.deconnexion} className={style.deconnexion}>
                  DÃ©connexion
                </a>
              </div>
            </div>
          ) : (
            <div>{connect.connectedComponent()}</div>
          )
        ) : connect.connexionComponentbyDefault ? (
          <div id={style.ConnexionDiv}>
            <form action={connect.connexionURL} method='get'>
              <button>
                <span>{resource.list.connected}</span>
                <br />
                <span>{resource.list.slash}</span>
                <br />
                <span>{resource.list.subscribe}</span>
              </button>
            </form>
          </div>
        ) : (
          <div>{connect.connexionComponent()}</div>
        )}
      </div>
    </header>
  )
}

export default Header
